*::before,
*::after {
  box-sizing: border-box;
}
* {
  margin: 0;
}
body {
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
  background-color: #182667;
  font-family: Arial, Helvetica, sans-serif;
  color: white;
}
img,
picture,
video,
canvas,
svg {
  display: block;
  max-width: 100%;
}
input,
button,
textarea,
select {
  font: inherit;
}
p,
h1,
h2,
h3,
h4,
h5,
h6 {
  overflow-wrap: break-word;
}
p {
  text-wrap: pretty;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  text-wrap: balance;
}

#app {
  display: flex;
  min-height: 100vh;
  justify-content: center;
}

.setup-div {
  display: flex;
  flex-direction: column;
  margin: auto;
  padding: 3rem;
  background-color: #101010;
  color: white;
  gap: 6px;
  font-size: 16px;
  align-items: center;
  justify-content: center;
  border-radius: 8px;
  -webkit-box-shadow: 2px 2px 8px 0px rgba(0,0,0,0.75);
  -moz-box-shadow: 2px 2px 8px 0px rgba(0,0,0,0.75);
  box-shadow: 2px 2px 8px 0px rgba(0,0,0,0.75);
}

.setup-div h1 {
  font-size: 3rem;
  font-weight: 700;
  line-height: 1;
}

.setup-div h2 {
  font-size: 2rem;
  font-weight: 600;
  line-height: 1;
  color:#b6b9bb;
  margin-bottom: 2rem;
}

.setup-div label {
  width: 100%;
  font-size: 20px;
  font-weight: 700;
}

.setup-div input {
  width: 100%;
  font-size: 18px;
  border-radius: 2px;
  border: none;
  outline: none;
  padding-left: 6px;
  margin-bottom: 2rem;
}

.setup-div button {
  background-color: #28a745;
  color: white;
  border: none;
  padding: 12px 20px;
  font-size: 18px;
  font-weight: 600;
  border-radius: 8px;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease;
}

.setup-div button:hover {
  background-color: #218838; /* Darker green */
  transform: scale(1.05); /* Slight zoom */
}

.grid-container {
  display: grid;
  grid-template-columns: repeat(10, 1fr); /* 10 equal columns */
  grid-template-rows: repeat(10, 1fr); /* 10 equal rows */
  gap: 2px; /* Space between cells */
  width: min(90vw, 500px); /* Make it responsive */
  height: auto; /* Height adjusts automatically */
}

.grid-cell {
  background-color: #1e1e1e;
  border: 1px solid #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  cursor: pointer;
  aspect-ratio: 1 / 1; /* Keeps each cell a perfect square */
}

.grid-cell:hover {
  background-color: lightblue;
}

.hit {
  background-color: red; /* Hits turn red */
}

.miss {
  background-color: gray; /* Misses turn gray */
}

.ship-container {
  display: flex;
  gap: 10px;
  margin-bottom: 20px;
}

.ship {
  background-color: steelblue;
  color: white;
  padding: 10px;
  cursor: grab;
  border-radius: 5px;
}

.ship-placed {
  background-color: darkgreen !important;
}`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAAA;;;EAGE,sBAAsB;AACxB;AACA;EACE,SAAS;AACX;AACA;EACE,gBAAgB;EAChB,mCAAmC;EACnC,yBAAyB;EACzB,yCAAyC;EACzC,YAAY;AACd;AACA;;;;;EAKE,cAAc;EACd,eAAe;AACjB;AACA;;;;EAIE,aAAa;AACf;AACA;;;;;;;EAOE,yBAAyB;AAC3B;AACA;EACE,iBAAiB;AACnB;AACA;;;;;;EAME,kBAAkB;AACpB;;AAEA;EACE,aAAa;EACb,iBAAiB;EACjB,uBAAuB;AACzB;;AAEA;EACE,aAAa;EACb,sBAAsB;EACtB,YAAY;EACZ,aAAa;EACb,yBAAyB;EACzB,YAAY;EACZ,QAAQ;EACR,eAAe;EACf,mBAAmB;EACnB,uBAAuB;EACvB,kBAAkB;EAClB,oDAAoD;EACpD,iDAAiD;EACjD,4CAA4C;AAC9C;;AAEA;EACE,eAAe;EACf,gBAAgB;EAChB,cAAc;AAChB;;AAEA;EACE,eAAe;EACf,gBAAgB;EAChB,cAAc;EACd,aAAa;EACb,mBAAmB;AACrB;;AAEA;EACE,WAAW;EACX,eAAe;EACf,gBAAgB;AAClB;;AAEA;EACE,WAAW;EACX,eAAe;EACf,kBAAkB;EAClB,YAAY;EACZ,aAAa;EACb,iBAAiB;EACjB,mBAAmB;AACrB;;AAEA;EACE,yBAAyB;EACzB,YAAY;EACZ,YAAY;EACZ,kBAAkB;EAClB,eAAe;EACf,gBAAgB;EAChB,kBAAkB;EAClB,eAAe;EACf,2DAA2D;AAC7D;;AAEA;EACE,yBAAyB,EAAE,iBAAiB;EAC5C,sBAAsB,EAAE,gBAAgB;AAC1C;;AAEA;EACE,aAAa;EACb,sCAAsC,EAAE,qBAAqB;EAC7D,mCAAmC,EAAE,kBAAkB;EACvD,QAAQ,EAAE,wBAAwB;EAClC,uBAAuB,EAAE,uBAAuB;EAChD,YAAY,EAAE,iCAAiC;AACjD;;AAEA;EACE,yBAAyB;EACzB,sBAAsB;EACtB,aAAa;EACb,mBAAmB;EACnB,uBAAuB;EACvB,YAAY;EACZ,eAAe;EACf,mBAAmB,EAAE,qCAAqC;AAC5D;;AAEA;EACE,2BAA2B;AAC7B;;AAEA;EACE,qBAAqB,EAAE,kBAAkB;AAC3C;;AAEA;EACE,sBAAsB,EAAE,qBAAqB;AAC/C;;AAEA;EACE,aAAa;EACb,SAAS;EACT,mBAAmB;AACrB;;AAEA;EACE,2BAA2B;EAC3B,YAAY;EACZ,aAAa;EACb,YAAY;EACZ,kBAAkB;AACpB;;AAEA;EACE,sCAAsC;AACxC","sourcesContent":["*,\n*::before,\n*::after {\n  box-sizing: border-box;\n}\n* {\n  margin: 0;\n}\nbody {\n  line-height: 1.5;\n  -webkit-font-smoothing: antialiased;\n  background-color: #182667;\n  font-family: Arial, Helvetica, sans-serif;\n  color: white;\n}\nimg,\npicture,\nvideo,\ncanvas,\nsvg {\n  display: block;\n  max-width: 100%;\n}\ninput,\nbutton,\ntextarea,\nselect {\n  font: inherit;\n}\np,\nh1,\nh2,\nh3,\nh4,\nh5,\nh6 {\n  overflow-wrap: break-word;\n}\np {\n  text-wrap: pretty;\n}\nh1,\nh2,\nh3,\nh4,\nh